@import url(https://fonts.googleapis.com/css?family=Archivo|Oswald|Work+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.switch {
    bottom: 5px;
}

.flex-container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.switch input {
    position: absolute;
    top: 0;
    z-index: 2;
    opacity: 0;
    cursor: pointer;
}

.switch input:checked {
    z-index: 1;
}

.switch input:checked+label {
    opacity: 1;
    cursor: default;
}

.switch input:not(:checked)+label:hover {
    opacity: 0.5;
}

.switch label {
    color: #fff;
    font-size: 18px;
    opacity: 0.33;
    -webkit-transition: opacity 0.25s ease;
    transition: opacity 0.25s ease;
    cursor: pointer;
}

.switch .toggle-outside {
    height: 40px;
    border-radius: 2rem;
    padding: 0.25rem;
    overflow: hidden;
    -webkit-transition: 0.25s ease all;
    transition: 0.25s ease all;
}

.switch .toggle-inside {
    border-radius: 5rem;
    background: #fff;
    position: absolute;
    -webkit-transition: 0.25s ease all;
    transition: 0.25s ease all;
}

.switch--horizontal {
    width: 18rem;
    height: 3rem;
    margin: 0 auto;
    font-size: 0;
    margin-bottom: 1rem;
}

.switch--horizontal input {
    height: 3rem;
    width: 6rem;
    left: 6rem;
    margin: 0;
}

.switch--horizontal label {
    font-size: 18px;
    line-height: 3rem;
    display: inline-block;
    width: 6rem;
    height: 100%;
    margin: 0;
    text-align: center;
}

.switch--horizontal label:last-of-type {
    margin-left: 3rem;
}

.switch--horizontal .toggle-outside {
    background: transparent;
    position: absolute;
    border-style: solid;
    border-width: 1px;
    border-color: white;
    width: 5rem;
    left: 5rem;
}

.switch--horizontal .toggle-inside {
    height: 2rem;
    width: 2rem;
    bottom: 3px;
}

@media only screen and (max-width: 768px) {
    .switch--horizontal .toggle-inside {
        bottom: 5px;
    }
}

.switch--horizontal input:checked~.toggle-outside .toggle-inside {
    left: 0.25rem;
}

.switch--horizontal input~input:checked~.toggle-outside .toggle-inside {
    left: 2.7rem;
}
.select--arrow {
    width: 30px;
    height: 30px;
    position: absolute;
    right: 10px;
    top: 30px;
    margin: 0;
    padding: 0;

}

.select--container {
    width: 100%;
    margin: 0;
    height: 55px;
    border: 1px solid #ced4da;
    box-sizing: border-box;
    display: inline-block;
    background: white;
    background-clip: padding-box;
    border-radius: .3rem;
    -webkit-transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
}

.selected {
    /* background: #54C6EB; */
}

.currencies {
    padding-right: 0;
    padding-left: 0;
}

.currencies:hover {
    /* background: #E8E8E8; */
}

.select--selected-item {
    display: inline-block;
    height: 100%;
    width: 100%;
    padding: 0px 0 0 5px;
    vertical-align: middle;
}

.flag {

    width: 45px;
    height: 45px;
}



.select--items {
    background: white;
    z-index: 1;
    margin-top: 10px;
    position: absolute;
    border-radius: .3rem;
    padding: 8px;

}

.select--arrow-down {
    position: absolute;
    top: 12px;
    left: 8px;
    width: 0;
    height: 0;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-top: 10px solid #993368;
}

.select--arrow-up {
    position: absolute;
    top: 10px;
    left: 8px;
    width: 0;
    height: 0;
    border-left: 8px solid transparent;
    border-right: 8px solid transparent;
    border-bottom: 10px solid #993368;
}


